.menu-bar{
    align-items: center;
    text-align: center;
    display: block;
    position: fixed;
    top: 0px;
    height: 100vh;
    min-width: calc(10px + 30vmin);
    left: calc(0px + 0vmin);
    background-color: rgba(10, 44, 52, 0.5);
    font-size: calc(10px + 2vmin);
    color: white;
    z-index: 1;
    font-weight: bolder;
}

.listStyle{
    top: 20vh;
    left: 30px;
    position: absolute;
    align-items: center;
    text-align: center;
    list-style-type: none;
}

.focusedClass{
    color: gold;
}