.main-body{
    display: block;
    position: fixed;
    height: 100vh;
    /*left: calc(0px + 20vw);*/
    min-width: 100vw;
    align-items: center;
    justify-content: center;
    background-color: black;
    font-size: calc(10px + 2vmin);
    color: white;
    z-index: 0;
}


.toolTip{
    position: absolute;
    text-align: center;
    width: 60px;
    height: 28px;
    padding: 2px;
    font-size: calc(1px + 1.7vmin);
    /*background: blanchedalmond;*/
    border: 0px;
    border-radius: 8px;
    pointer-events: none;
}